@font-face {
  font-family: "Blogger Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 700;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/bloggersans-italic.woff2") format("woff2"), url("../fonts/bloggersans-italic.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 300;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 500;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/bloggersans-mediumitalic.woff2") format("woff2"), url("../fonts/bloggersans-mediumitalic.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 25px;
  padding-left: 25px;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-right: 60px;
    padding-left: 60px;
  }
}

.page {
  height: 100%;
  font-size: 16px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #edeff6;
  color: #444444;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}

.main {
  padding-top: 78px;
}
@media (min-width: 768px) {
  .main {
    padding-top: 139px;
  }
}
@media (min-width: 1440px) {
  .main {
    padding-top: 106px;
  }
}

@media (min-width: 1440px) {
  .main__row {
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
}

.main--nojs {
  padding-top: 0;
}
@media (min-width: 768px) {
  .main--nojs {
    padding-top: 0;
  }
}
@media (min-width: 1440px) {
  .main--nojs {
    padding-top: 0;
  }
}

.main-nav {
  padding-top: 42px;
  padding-bottom: 51px;
}
@media (min-width: 768px) {
  .main-nav {
    padding-left: 57px;
    padding-top: 52px;
    padding-bottom: 129px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    padding-top: 67px;
    padding-left: 111px;
    padding-bottom: 0;
  }
}
.main-nav--scroll .main-nav__list::before {
  background-color: #192144;
}
@media (min-width: 1440px) {
  .main-nav--scroll .main-nav__link {
    color: #192144;
  }
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__list {
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    display: flex;
    position: relative;
  }
  .main-nav__list::before {
    content: "";
    display: block;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    left: -71px;
    top: 7px;
  }
}

.main-nav__link {
  font-weight: 500;
  font-size: 30px;
  line-height: 35px;
  color: #192144;
  text-transform: uppercase;
}
.main-nav__link:focus, .main-nav__link:hover {
  opacity: 0.7;
  outline: none;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 60px;
    line-height: 71px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 20px;
    margin-left: 34px;
  }
}

.login-button {
  display: block;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 8px;
  color: #161c35;
  background: #ffd74b;
  border-radius: 25px;
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  padding-right: 30px;
  padding-left: 30px;
}
.login-button:focus, .login-button:hover {
  background-color: #ffbd30;
  outline: none;
}
.login-button:active {
  background-color: #ffbd30;
  color: rgba(22, 28, 53, 0.3);
}
@media (min-width: 768px) {
  .login-button {
    font-size: 20px;
  }
}

.contact {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1440px) {
  .contact {
    display: flex;
    flex-wrap: wrap;
  }
}

.contact__link:focus, .contact__link:hover {
  outline: none;
}
.contact__link:focus .contact__link-text, .contact__link:hover .contact__link-text {
  color: #ffbd30;
}
.contact__link:focus .contact__link-img, .contact__link:hover .contact__link-img {
  fill: #ffbd30;
}
.contact__link:active {
  opacity: 0.3;
}

.contact__link-icon {
  display: inline-block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #121936;
  text-align: center;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .contact__link-icon {
    width: 68px;
    height: 68px;
    vertical-align: baseline;
  }
}
@media (min-width: 1440px) {
  .contact__link-icon {
    width: 90px;
    height: 90px;
  }
}

.contact__link-img {
  display: inline-block;
}
.contact__link-img--tel {
  width: 15.7px;
  height: 14.83px;
  margin-top: 15px;
  fill: #ffd74b;
}
@media (min-width: 768px) {
  .contact__link-img--tel {
    width: 23.7px;
    height: 22.4px;
    margin-top: 23px;
  }
}
@media (min-width: 1440px) {
  .contact__link-img--tel {
    width: 36px;
    height: 34px;
    margin-top: 30.1px;
  }
}
.contact__link-img--mail {
  width: 18.28px;
  height: 12.5px;
  margin-top: 17px;
  fill: #ffd74b;
}
@media (min-width: 768px) {
  .contact__link-img--mail {
    width: 27.6px;
    height: 18.8px;
    margin-top: 26px;
  }
}
@media (min-width: 1440px) {
  .contact__link-img--mail {
    width: 36.5px;
    height: 25px;
    margin-top: 34px;
  }
}

.contact__link-text {
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #ffd74b;
  text-transform: uppercase;
  margin-left: 16px;
}
.contact__link-text--blue {
  color: #192144;
}
@media (min-width: 768px) {
  .contact__link-text {
    font-size: 28px;
    line-height: 28px;
    margin-left: 22px;
  }
}
@media (min-width: 1440px) {
  .contact__link-text {
    margin-left: 31px;
    vertical-align: top;
    padding-top: 37px;
  }
}

.contact--mini {
  display: flex;
}
.contact--mini .contact__item:last-child {
  flex-grow: 1;
}
.contact--mini .contact__link-text {
  display: none;
}
.contact--mini .contact__link-icon {
  width: 40px;
  height: 40px;
}
.contact--mini .contact__link-icon--tel {
  background-color: #ff8d30;
}
.contact--mini .contact__link-icon--mail {
  background-color: #ffbd30;
}
.contact--mini .contact__link-img {
  fill: #161c35;
}
.contact--mini .contact__link-img--tel {
  margin-top: 12.7px;
}
.contact--mini .contact__link-img--mail {
  margin-top: 13.9px;
}
.contact--mini:focus .contact__link-icon--tel, .contact--mini:hover .contact__link-icon--tel {
  background-color: #f26926;
}
.contact--mini:focus .contact__link-icon--mail, .contact--mini:hover .contact__link-icon--mail {
  background-color: #ff8d30;
}
.contact--mini:focus .contact__link-img, .contact--mini:hover .contact__link-img {
  fill: #161c35;
}

.contact--mini-in-desktop .contact__link-text {
  margin-left: 4%;
}
@media (min-width: 768px) {
  .contact--mini-in-desktop .contact__link-text {
    margin-left: 3%;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-text {
    display: none;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-icon {
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-icon--tel {
    background-color: #ff8d30;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-icon--mail {
    background-color: #ffbd30;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-img {
    fill: #161c35;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-img--tel {
    width: 15.9px;
    height: 16.2px;
    margin-top: 16px;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link-img--mail {
    width: 14.4px;
    height: 11.2px;
    margin-top: 19px;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link:focus .contact__link-icon--tel, .contact--mini-in-desktop .contact__link:hover .contact__link-icon--tel {
    background-color: #f26926;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link:focus .contact__link-icon--mail, .contact--mini-in-desktop .contact__link:hover .contact__link-icon--mail {
    background-color: #ff8d30;
  }
}
@media (min-width: 1440px) {
  .contact--mini-in-desktop .contact__link:focus .contact__link-img, .contact--mini-in-desktop .contact__link:hover .contact__link-img {
    fill: #161c35;
  }
}

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .socials__list--left {
    justify-content: left;
  }
}

.socials__link {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .socials__link {
    width: 62px;
    height: 62px;
  }
}
.socials__link::after {
  content: "";
  display: block;
  width: 45px;
  height: 45px;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .socials__link::after {
    width: 62px;
    height: 62px;
  }
}
.socials__link:focus, .socials__link:hover {
  outline: none;
}
.socials__link:focus::after, .socials__link:hover::after {
  opacity: 0.6;
}
.socials__link:active::after {
  opacity: 0.3;
}
.socials__link--yellow {
  background-color: #ffd74b;
}
.socials__link--yellow:focus, .socials__link--yellow:hover {
  background-color: #ffbd30;
  opacity: 1;
}
.socials__link--yellow:focus::after, .socials__link--yellow:hover::after {
  opacity: 1;
}
.socials__link--yellow:active {
  opacity: 1;
}
.socials__link--yellow:active::after {
  opacity: 0.3;
}
.socials__link--fb::after {
  background-image: url("../img/facebook.svg");
  background-size: 11px, 21px;
}
@media (min-width: 768px) {
  .socials__link--fb::after {
    background-size: 13.5px, 27.7px;
  }
}
.socials__link--vk::after {
  background-image: url("../img/vk.svg");
  background-size: 23px, 12px;
}
@media (min-width: 768px) {
  .socials__link--vk::after {
    background-size: 30px, 15.8px;
  }
}
.socials__link--inst::after {
  background-image: url("../img/instagram.svg");
  background-size: 18px, 18px;
}
@media (min-width: 768px) {
  .socials__link--inst::after {
    background-size: 24px, 24px;
  }
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  background-color: #161c35;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .header {
    background-color: #161c35;
  }
}

.header__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content min-content min-content min-content;
}
@media (min-width: 768px) {
  .header__wrapper {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: min-content min-content min-content min-content;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    grid-template-columns: 200px 2fr 1fr auto;
    grid-template-rows: min-content;
  }
}

.header__logo {
  grid-row: 1;
  grid-column: 1;
  padding-top: 27px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .header__logo {
    padding-top: 45px;
    padding-bottom: 38px;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    padding-top: 47px;
    padding-bottom: 0;
  }
}

.header__button-wrapper {
  grid-row: 1;
  grid-column: 2;
  text-align: right;
  padding-top: 20px;
  padding-bottom: 26px;
}
@media (min-width: 768px) {
  .header__button-wrapper {
    text-align: center;
    padding-top: 52px;
    padding-bottom: 54px;
  }
}
@media (min-width: 1440px) {
  .header__button-wrapper {
    display: none;
  }
}

.header__button {
  display: inline-block;
  border: none;
  background-color: transparent;
  width: 22px;
  height: 22px;
  padding: 0;
  padding-top: 4px;
}
.header__button:focus, .header__button:hover {
  opacity: 0.3;
  outline: none;
}
.header__button:active {
  opacity: 0.15;
}
@media (min-width: 768px) {
  .header__button {
    width: 22px;
    height: 22px;
  }
}

.header__button-img {
  width: 22px;
  height: 22px;
  fill: #ffffff;
}
@media (min-width: 768px) {
  .header__button-img {
    width: 22px;
    height: 22px;
  }
}

.header__login {
  display: none;
  grid-column: 1/3;
  grid-row: 3;
  margin-bottom: 51px;
}
@media (min-width: 768px) {
  .header__login {
    display: block;
    grid-column: 3;
    grid-row: 1;
    padding-left: 18%;
    padding-top: 41px;
    padding-bottom: 41px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .header__login {
    grid-column: 4;
    padding-bottom: 0;
    padding-left: 0;
    padding-top: 49px;
    max-width: 200px;
  }
}

.header__nav {
  display: none;
  grid-column: 1/3;
  grid-row: 2;
}
@media (min-width: 768px) {
  .header__nav {
    grid-column: 1/4;
  }
}
@media (min-width: 1440px) {
  .header__nav {
    display: block;
    grid-row: 1;
    grid-column: 2;
  }
}

.header__contact {
  grid-column: 1/3;
  grid-row: 4;
  margin-bottom: 29px;
  display: none;
}
@media (min-width: 768px) {
  .header__contact {
    grid-column: 1/4;
    grid-row: 3;
    padding-left: 8.3%;
    margin-bottom: 113px;
  }
}
@media (min-width: 1440px) {
  .header__contact {
    grid-column: 3;
    grid-row: 1;
    margin-bottom: 0;
    padding-top: 49px;
    display: flex;
    justify-content: flex-end;
  }
}

.header__socials {
  display: none;
  grid-column: 1/3;
  grid-row: 5;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .header__socials {
    grid-column: 1/4;
    grid-row: 4;
    padding-left: 8.3%;
    margin-bottom: 73px;
  }
}

.header--opened {
  background-color: #ffffff;
}
@media (min-width: 1440px) {
  .header--opened {
    background-color: #161c35;
  }
}
.header--opened .header__button-img {
