@media (min-width: 768px) {
  .header--scroll .header__logo {
    background-image: url("../img/logo-tablet-blue@1x.png");
    background-size: 162px auto;
  }
}
@media (min-width: 768px) and (max-resolution: 2dppx) {
  .header--scroll .header__logo {
    background-image: url("../img/logo-tablet-blue@2x.png");
  }
}
@media (min-width: 1440px) {
  .header--scroll .header__logo {
    background-image: url("../img/logo-desktop-blue@1x.png");
    background-size: 200px auto;
  }
}
@media (min-width: 1440px) and (max-resolution: 2dppx) {
  .header--scroll .header__logo {
    background-image: url("../img/logo-desktop-blue@2x.png");
  }
}
@media (min-width: 1440px) {
  .header--scroll .header__login {
    padding-top: 21px;
    padding-bottom: 19px;
  }
}
@media (min-width: 1440px) {
  .header--scroll .header__nav {
    padding-top: 39px;
    padding-bottom: 19px;
  }
}
