.promo-list {
  counter-reset: myCounter;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.promo-list__item {
  list-style: none;
  position: relative;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: $basic-blue-dark;
  border-radius: 10px;
  background-color: $basic-yellow;
  width: 100%;
  min-height: 100px;
  margin-top: -20px;
  padding-top: 22px;
  padding-left: 24px;
  padding-bottom: 20px;
  padding-right: 96px;

  &::after {
    counter-increment: myCounter;
    content: counter(myCounter);
    display: block;
    position: absolute;
    top: 22px;
    right: 21px;
    color: $basic-white;
  }

  @media (min-width: $tablet-width) {
    width: 50%;
    margin-top: 0;
    border-radius: 0;
    min-height: 140px;
    font-size: 24px;
    line-height: 24px;
    padding-top: 31px;
    padding-left: 32.6px;
    padding-bottom: 61px;

    &::after {
      font-size: 30px;
      line-height: 30px;
      top: inherit;
      right: inherit;
      bottom: 22px;
      left: 33px;
    }
  }

  @media (min-width: $desktop-width) {
    min-height: 106px;
    padding-top: 30px;
    padding-left: 34px;
    padding-bottom: 30px;
    padding-right: 107px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;

    &::after {
      font-size: 24px;
      line-height: 24px;
      bottom: inherit;
      left: inherit;
      top: 29px;
      right: 64px;
    }
  }

  &--1 {
    background-color: $special-orange;
    margin-top: 0;

    @media (min-width: $tablet-width) {
      border-top-left-radius: 20px;
      width: calc(50% + 20px);
    }

    @media (min-width: $desktop-width) {
      width: 345px;
    }
  }

  &--2 {
    background-color: $special-orange-light;

    @media (min-width: $tablet-width) {
      margin-left: -20px;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }

    @media (min-width: $desktop-width) {
      width: 375px;
      margin-left: -30px;
      border-top-right-radius: 0;
    }
  }

  &--3 {
    background-color: $special-yellow-dark;

    @media (min-width: $tablet-width) {
      border-bottom-left-radius: 20px;
      width: calc(50% + 20px);
    }

    @media (min-width: $desktop-width) {
      width: 375px;
      margin-left: -30px;
    }
  }

  &--4 {
    background-color: $basic-yellow;
    min-height: 80px;

    @media (min-width: $tablet-width) {
      margin-left: -20px;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    @media (min-width: $desktop-width) {
      border-radius: 20px;
      margin-left: -30px;
      width: 315px;

      &::after {
        right: 33px;
      }
    }
  }
}
